#visual {
	width: 100%;
	height: 100vh;
	position: absolute;
	top: 0;
	left: 0;
	z-index: 1;

	video {
		width: 100%;
		height: 100%;
		border: none;
		object-fit: cover;
	}
	.inner {
		width: 1180px;

		h1 {
			font: 80px/1.2 'Playfair Display';
			font-weight: 400;
			letter-spacing: 1px;
			color: #fff;
			position: absolute;
			top: 52%;
			left: 30%;
			transform: translate(-45%, -10%);

			span {
				margin-left: 15vw;
			}
		}
	}
}

